import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Route } from '@angular/router';
import { AmSharedModule } from '../../shared/am-shared.module';
import { AddSubusersComponent} from './add-subusers.component';

const ADD_SUBUSERS_ROUTES: Route[] = [
  {
    path: '', component: AddSubusersComponent
  }
];

@NgModule({
  declarations: [AddSubusersComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(ADD_SUBUSERS_ROUTES),
    AmSharedModule
  ]
})
export class AddSubusersModule { }
